/***********************/
html {
  scroll-behavior: smooth;
}
/***********Ours****/
.serv___cc{
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: column;
  width: 70vw;
  align-items: center;
}
/******/
.font___label span {
  font-size: 14px;
  color:#fff;
}
.chatbot___e{
  width: 70vw;
  padding-bottom: 50px;
  margin-top: 40px;
}
div#root {
    max-width: 100vw;
    overflow: hidden;
}
.container___sercive_c {
    width: 24vw;
    background: #fff;
    height: 25vw;
    box-shadow: 0 0 2px 1px #837d7d40;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px 5px;
    cursor: pointer;
    transition: background 0.7s ease-in;
    min-width: 240px;
    min-height: 320px;
}
svg.icons___sercive_c {
    width: 12vw;
    height: 12vw;
    display: flex;
    align-self: baseline;
    margin-left: 20px;
    margin-right: 20px;
    transition: color 0.7s ease-in;    

}
.title___sercive_c {
    color: #1a313f;
    font-size: 20px;
    font-weight: 600;
    margin: 14px;
    margin-left: 15%;
    display: flex;
    align-self: baseline;
    margin-top: 20px;
    transition: color 0.7s ease-in;
}
.cc__le___sercive_c {
    width: 95vw;
    display: flex;
    justify-content: space-around;
}
.desc____sercive_c {
    margin-top: 20px;
    text-align-last: center;
    transition: all 0.7s ease-in;
    width: 90%;
    font-size: 14px;
    font-weight: 400;
    font-family: system-ui;
}
.lng____e__ey {
    display: flex;
    position: fixed;
    top: 50%;
    margin-top: -20px;
    width: 135px;
    background: #fff;
    box-shadow: rgb(131 125 125 / 25%) 0px 0px 2px 2px;
    border-radius: 5px;
    left: -135px;
    z-index: 1999;
    transition:left .9s ease-in-out;

}
.lng____e__ey_02_ele {
    padding: 8px 18px;
    cursor: pointer;
    background: #fff;
}
.lng___lab_t {
    padding: 8px;
    font-size: 17px;
}
.lng____e__ey_02_ele:hover{background: #e0e0e0;}
.lng____e__ey_02 {
    overflow: hidden;
    width: 100%;
    border-radius: 5px;
    z-index: 88;
}
.lng____e__ey_01 {
    position: absolute;
    cursor: pointer;
    display: flex;
    right: -55px;
    width: 45px;
    padding: 8px 5px;
    top: 0;
    box-shadow: rgb(131 125 125 / 25%) 0px 0px 2px 2px;
    background: #fff;
   
    cursor: pointer;
    transition:right .7s ease;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}
.lng____e__ey_03 {
    position: absolute;
    cursor: pointer;
    right: -45px;
    width: 30px;
    height: 30px;
    top: -5px;
    z-index: 80;
    cursor: pointer;
    transition:right .7s ease;
    border-radius: 50%;
    opacity: 1;
}
.lng____e__ey_03_el_svg{
    width: 0;
    color: #0075ff;
    border-radius: 50%;
    background-color: #fff;
    transition:width .3s ease;
}
@media only screen and (max-width: 810px) {
    .cc__le___sercive_c{flex-direction: column;}
    svg.icons___sercive_c{align-self: center;margin-left: 0;}
    .title___sercive_c{align-self: center;margin: 5px;}
    .container___sercive_c {
        width: 80%;
        min-height: auto;
        margin: 10px;
        margin-left: auto;
        margin-right: auto;
        height: fit-content;
    }
}
